body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.suggest-input{
    position: relative;
}

.input {
    box-sizing: border-box;
    width: 100%;
    height: 34px;
    padding: 0 10px;
    font-size: 18px;
    border: 1px solid #cecece;
}

.suggest-block {
    box-sizing: border-box;
    overflow: auto;
    position: absolute;
    z-index: 100;
    width: 100%;
    max-height: 70vh;
    margin: 1px 0 0;
    padding: 0;
    list-style: none;
    background-color: #fff;
    border: 1px solid #cecece;
    border-top-width: 0;
}

.suggest-item {
    padding: 6px 10px;
    font-size: 18px;
    line-height: 16px;
}

.suggest-item:hover{
    background-color: #eaeaea;
    cursor: pointer;
}

.suggest-item.isActive {
    background-color: #dadada;
}



.app {
  padding: 40px;
}

.search-first {
  width: 100%;
  max-width: 400px;
}

.search-second {
  width: 100%;
  max-width: 600px;
}
